<template>
    <div>
         
            <div class="WT"  v-for="index in PD" :key="index.x"> 
                <div class="bg_top"> 
                    <span class="BT">{{index.groupName}}</span> 
                </div>
                 <div class="card" v-for="item2 in index.seconds" :key="item2.i" @click="todata(item2)">
                        <img class="img" :src="item2.pic" alt="">
                        <p>{{item2.groupName}}</p>
                    </div> 
            </div>  
            <!-- 到时候删掉    -->
            <div class="WT"> 
                <div class="bg_top"> 
                    <span class="BT">智慧校园设备</span> 
                </div>
                <a href="#/iph_class">
                    <div class="card">
                            <img class="img" src="../ready_delete/./delete_img/xinxifabuzhuji.png" alt="">
                            <p>信息发布主机</p>
                    </div>
                 </a> 
                 <a href="#/ecb_class">
                     <div class="card">
                            <img class="img" src="../ready_delete/./delete_img/dianzibanpai.png" alt="">
                            <p>电子班牌</p>
                    </div>
                 </a>
                 <a href="#/lcd_class">
                     <div class="card">
                            <img class="img" src="../ready_delete/./delete_img/lcdping.png" alt="">
                            <p>LCD屏</p>
                    </div>
                 </a>
            </div>   

            <div class="WT"> 
                <div class="bg_top"> 
                    <span class="BT">应用系统</span> 
                </div>
                <a href="#/irs_title">
                    <div class="card">
                            <img class="img" src="../ready_delete/./delete_img/xinxifabuxitong.png" alt="">
                            <p>信息发布系统</p>
                    </div>
                 </a> 
                 <a href="#/eas_title">
                    <div class="card">
                            <img class="img" src="../ready_delete/./delete_img/xiawuguanli.png" alt="">
                            <p>教务系统</p>
                    </div> 
                </a>
            </div> 
            <!-- //到时候删掉    -->     
        </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //产品
            PD:[],
        }
    },
    created(){
        this.forProduct()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods: {
         //获取产品项
        forProduct(){
            let url=`${this.$store.state.ipfg}/goodsGroup/findAll`
            Axios.get(url).then(resp=>{
                console.log('hhhhhhhhhhhhhhhhhhh')
                console.log(resp)
                this.PD=resp.data.data
            })
        },
        //路由传参
        todata(index){
            this.$router.push({path:'/product_list',query:{'id':index.id}})
        }
    },
    store
}
</script>
<style scoped>
.bg_top{
    height: 60px;
    background: url(../../../assets/topimg/topimg.png) no-repeat center center;
    background-size:100%; 
}
 .card{
     margin: 15px;
     display: inline-block;
     width: 207px;
     height: 170px;
     transition: 0.3s;
     cursor: pointer;
     color: #000;
 }
 .card>.img{
     width: 100%;
     height: 100%;
 }
 .card>p{
     text-align: center;
 }
 .WT{
     position: relative;
 }
 .WT>.bg_top>.BT{
     position: absolute;
     top: 16px;
     left: 40px;
     font-weight: 700;
 }
</style>



