<template>
    <div>
        <div class="inner">
            <div class="center">
                <!-- 面包屑 -->
                <div style="position: absolute">
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>意通新闻</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <!-- 大标题 -->
                <div class="main_font">
                        <h3>----------&nbsp;&nbsp;&nbsp;意通新闻&nbsp;&nbsp;&nbsp;----------</h3> 
                </div>
                <!-- 主要新闻 -->
                <div>
                    <div style="font-size:0px;">
                        <div class="squ" v-for="(index) in news" :key="index.s" @click="look(index)">
                            <img  :src="index.image" alt="" class="toImg">
                            <div class="bot_font">
                                <p>&nbsp;&nbsp;&nbsp;&nbsp;{{index.title}}</p>
                                 <a class="new_tittle" >详细内容>>&nbsp;&nbsp;&nbsp;&nbsp;</a>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- 次要新闻 -->
                        <!-- <div class="noimp">
                            <div v-for="(index,num) in news" :v-if="num>6" :key="index.b">
                                <p>{{num}}{{index.title}}</p>
                            </div>
                        </div> -->
                <div style="height:100px;"></div>
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios'
import store from '../vuex/store'
export default {
    data() {
        return {
            //主要新闻
            news:[]
        }
    },
    created(){
        // console.log(this.$store.state.ipfg)
        this.forNews()
    },
    methods: {
        //获取公司新闻
        forNews(){
            let url =`${this.$store.state.ipfg}/companyNews/findAll/1/5`
            // console.log(url)
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.news=resp.data.data.content
                
            })
        },
        //查看新闻
        look(value){
            // console.log(value)
            //this.$router.push({path:`/lookNews:/${value}`})
             this.$router.push({name:'lookNews',query:{'val':value.id}})
        }

    },
    store,
}
</script>
<style scoped>
.inner{
    width: 1220px;
    /* height: 2000px; */
    min-height: 700px;
    margin: 83px auto 0px;
    /* background: darkmagenta */
}
.main_font{
        margin-top: 100px;
        height: 100px;
        line-height: 100px;
        /* border-bottom:1px solid #737476; */
    }
    .main_font>h3{
        text-align: center;
        color: #737476;
    }
    .squ{
        position: relative;
        overflow: hidden;
        cursor: pointer;  
        font-size: 16px;
        transition: 0.3s;
    }

    .squ:nth-child(1),.squ:nth-child(2){
        display: inline-block;
        width: 600px;
        height: 300px;
        margin: 0;
        border: 1px solid  rgba(225, 225, 225, .8);
    }
    .squ:nth-child(3),.squ:nth-child(4),.squ:nth-child(5){
        display: inline-block;
        width: 400px;
        height: 300px;
        margin: 0;
        border: 1px solid  rgba(225, 225, 225, .8);
    }
    .squ>.bot_font{
        height: 50px;
        width: 100%;
        background: rgba(225, 225, 225, .5);
        position: absolute;
        bottom: 0;  
    }
    .squ>.bot_font>.new_tittle{
        text-decoration: none;
        /* color: #000; */
        position: absolute;
        right: 0;
    }
    .squ>.toImg{
        width: 100%;
        height: 100%;
    }

</style>



