<template>
    <div>
        <div class="inner">
            <!-- 面包屑 -->
            <div style="height:15px;"></div>
                <div>
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item :to="{path:'/recruit'}">招聘</el-breadcrumb-item>
                        <el-breadcrumb-item>{{RD.postNames}}</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <div style="height:50px;"></div>
            <!-- 职位部分 -->
            <div class="up_rd">
                <span>更新于{{RD.updateDates}}</span>
                <h3>{{RD.postNames}}</h3>
                <span>{{RD.workPlaces}}&nbsp;&nbsp;|&nbsp;&nbsp;{{RD.experences}}&nbsp;&nbsp;|&nbsp;&nbsp;{{RD.requires}}</span>
                <span>邮箱:{{RD.mails}}</span>
                <a href="#/uploadResume" class="bot_rig">
                <el-button type="primary">&nbsp;&nbsp;&nbsp;点击报名&nbsp;&nbsp;&nbsp;</el-button>
                </a>
                
            </div>
            <!-- 职位描述 -->
            <div class="describe">
                <p>
                   <span class="font">职位描述</span>
                   <span class="hrs"><hr></span>
                </p>
                <p v-html="RD.qualifications"></p>
            </div>
            <!-- 岗位职责 -->
            <div class="responsibility">
                <p>
                   <span class="font">岗位职责</span>
                   <span class="hrs"><hr></span>
                </p>
                <p v-html="RD.dutys"></p>
            </div>
        </div>
    </div>
</template>
<script>
import store from '../vuex/store'
import Axios from 'axios';
export default {
    data() {
        return {
            RD:'',
        }
    },
    created(){
        // console.log(this.$route.query.id)
        this.recruitmentDetails()
    },
    methods:{
        //获取招聘详情
       recruitmentDetails(){
           let url=`${this.$store.state.ipfg}/recruitInfo/findOne/${this.$route.query.id}`
           Axios.get(url).then(resp=>{
               console.log(resp)
               this.RD=resp.data.data
               this.RD.qualifications=this.RD.qualifications.replace(/；/g,';<br>')
               this.RD.dutys=this.RD.dutys.replace(/；/g,';<br>')
           })
       }
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 700px;
    /* height: 2000px; */
    margin: 83px auto 0px;
    /* background: darkmagenta */
}
.up_rd{
    height: 130px;
    background: rgba(6, 172, 172, 0.2);
    padding: 40px;
    position: relative;
    line-height: 40px;
}
.describe,.responsibility{
    position: relative;
    margin-top: 30px;
    line-height: 25px;
}
.responsibility:after{
    content: '';
    height: 100px;
    display: block;
}
.describe>p>.font{
    font-weight: 600;
    font-size: 17px;
    line-height: 40px;
}
.responsibility>p>.font{
    font-weight: 600;
    font-size: 17px;
    line-height: 40px;
}
.bot_rig{
    position: absolute;
    right: 50px;
    top: 100px;
}

</style>



