<template>
    <div>
        <div class="inner">
            <div class="center">
                <!-- 面包屑 -->
                <div style="position: absolute">
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>行业新闻</el-breadcrumb-item>
                         <el-breadcrumb-item>{{inner_data.title}}</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <div class="neirong">
                    <div style="height:50px;"></div>
                    <h2>{{inner_data.title}}</h2>
                    <p class="time">发布时间:{{inner_data.pubDate}}</p>
                    <hr>
                    <img :src="inner_data.image" alt="">
                    <p class="fir_p">{{inner_data.content}}</p>
                    
                    <div style="height:150px;"></div>
                </div>
                
            </div>
        </div>
       
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../vuex/store';
export default {
    data() {
        return {
            //新闻内容
            inner_data:''
        }
    },
    created(){
        console.log(this.$route.query.val)
        // this.inner_data=this.$route.query.val
        this.for_industry()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods: {
        for_industry(){
            let url=`${this.$store.state.ipfg}/industryNews/findOne/${this.$route.query.val}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.inner_data=resp.data.data
            })
        }
        
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 800px;
    margin: 83px auto 0px;
    /* background: darkmagenta */
}

.center{
    position: relative;
}
.center>.neirong>h2{
    line-height: 100px;
    text-align: center;
}
.center>.neirong>p{
    line-height: 25px;
}
.center>.neirong img{
    max-width: 400px;
    max-height: 400px;
    float: right;
    top:0px; 
    right:0;  
    margin-top: 25px;
}
.center>.neirong>.time{
    color: #737476;
    font-size: 14px;
    position: absolute;
    right: 20px;
    top: 98px;
}
.fir_p{
    margin-top: 20px;
}
</style>




