<script>
import echarts from 'echarts'
export default {
    data() {
        return {
            //图表
            dataList:[
               {value:32, name:'技术员32%'},
               {value:28, name:'工程师28%'},
               {value:20, name:'高级工程师20%'},
               {value:20, name:'管理人员20%'},
            //    {value:3, name:'81-100分'},
            ],
            //卡片
            currentDate: new Date(),
            //热销产品

        }
    },
    methods:{
         // 图表初始化数据
        initChart(){
            let myChart = echarts.init(this.$refs.myEchart);
            myChart.setOption({
                tooltip: {
                    trigger:'item',
                    formatter: "{a} <br/>{b}: {c} <br/> ({d}%)"
                },
                series : [
                    {
                        name:'深圳市意通信息技术有限公司',
                        type: 'pie',
                        radius : '60%',
                        center: ['52%', '53%'],
                        avoidLabelOverlap: false,
                        data: this.dataList,
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 5,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.3)'
                            }
                        },
                         label:{            //饼图图形上的文本标签
                            normal:{
                                textStyle : {
                                    fontSize : 20    //文字的字体大小
                                },
                            }
                         }
                    }
                ]
            })
        }
    },
     mounted () {
        let obj = this.$refs.myEchart;
        if(obj){
            this.initChart();
        }
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    }
}
</script>
