var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_vm._m(0),_vm._v(" "),_c('div',{staticClass:"main_sub2",attrs:{"id":"box_main_sub2"}},[_c('div',{staticClass:"prolist_four"},[_c('div',{staticClass:"pro_con_one"},[_c('div',{staticStyle:{"height":"80px"}}),_vm._v(" "),_vm._m(1),_vm._v(" "),_vm._m(2),_vm._v(" "),_c('el-tabs',{attrs:{"type":"border-card"}},[_c('el-tab-pane',{attrs:{"label":"学生工作"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t1、学生家长：根据选定的班级能够进行批量、指定、全选方式给家长进行留言；教师还可通过该模块查看学生及其监护人资料。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t★（1）批量留言：教师可选择多个学生，给多个学生家长批量留言。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t★（2）消息记录：可以看到教师给学生家长发送的留言，留言记录按时间线顺序展示。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（3）查看学生资料：学生信息详情，包含学校名称、班级、学号、姓名、性别、卡号、走读/住读、是否坐校车、主监护人、主手机号、副监护人以及副监护人手机号；"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t2、平时成绩：教师可通过该模块录入、导出、查询、分析、发送学生成绩。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（1）自动获取：采用图形化页面，基于智慧综合管理平台（序号1-13），自动筛选出与任课老师绑定的班级课程信息、班级集体照，教师不用进行输入科目、班级等操作；教师可对不同年级、班级进行筛选查看。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（2）补录考试：设置每次的考试信息，新建考试编号、考试名称、考试时间、考试类型、状态、学生成绩、考试科目、考试班级、考试分值。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（3）考试类型：分大考（月考、期中考、期末考）和小考（科目小测、班级测验），用后台发布考试时，考试类型固定，补录成绩时才需要选择该成绩的考试类型。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（4）考试名称：自动获取由智慧班牌模式应用模块（序号2）发布的考试模式的考试名称、考试学生、考场信息等；"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（5）导出导入：提供导入模板样式,并支持自动生成班级名称、学生姓名；导出学生成绩、分析图表、打印排序后的成绩表单。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（6）考试录入：在录入人员中设置的老师在这个界面有网上录入成绩的功能，对于异常状态的学生，系统自动按空或零分计算，不需要手工进行录入。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（7）记录维护：修改考试标题、时间、类型，编辑修改学生考试成绩。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（8）成绩发送：学生成绩主要是开放给学生个人、家长、也可发送到智慧班牌，方便学生查看自己每次考试的成绩。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（9）班级成绩：班主任查看班级学生各科考试分数、考试状态信息，并可以EXCEL形式导出，保存和数据使用。同一个班级的各科科任老师可以看到班级学生各科考试分数、考试状态等信息，方便全方位了解学生的学习情况和学习状态，并可以导出EXCEL，更易于保存和数据处理。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（10）年级成绩：查看全校各班的班级考试情况，包含学生各科考试分数、等级和考试状态信息。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（11）考试记录：选择任课班级查看该班级以往考试记录，对每次的考试分数记录，支持在线查看分析；考试栏的项目可通过时间区间、考试类型、考试科目等条件进行筛选，方便教师精确查找考试。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t3、学生考勤：系统自动统计学生出勤情况，教师可按照年级/班级/指定学生进行查看，支持考勤数据导出；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t4、学生请假：学生请假审批与记录查看；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t5、课堂考勤：走班学生上课考勤统计与查看；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t6、健康体检：通过仪器对学生健康状态进行检测，以便学校、学生、家长了解学生健康状况，预防疾病的发生；同时为学生建立健康成长档案，学生通过体检机测试的结果，如体重、身高、体质指数、体温、色检、视力、头像信息，可以通过年级、班级、姓名、时间进行查询，并可以对体检数据进行导出； "),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t7、成绩分析：将每次的学生考试成绩作为一次档案记录，并支持对每次的记录做数据分析，包括分数段统计、成绩排名、单科排名、多科排名、综合排名、综合标准分、班级平均分分析。支持查询调整已经录入的成绩信息，只有补录的考试可以删除，系统发布的考试不可删除；在成绩表格上也可以设置最大分值； "),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t8、大型考试成绩分析："),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t智慧校园综合管理平台的最重要功能之一，前置条件是“平时成绩”，要完整的使用“考试分析”功能，必须在“平时成绩”完成成绩录入。考试分析分为两种类型，共8种分析目的，满足各种成绩分析的需求："),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（1）单次考试分析"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t①.成绩分布统计：分析考试分数的分布，自行设置分数分布段，自动更新表格数据并生成饼图。表格统计项目也可自由选择，不勾选则不统计。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t②.班级对比分析：班级之间各种数据的对比，分析各班级各个方面的差距，例如语文单科成绩各班之间平均分、优秀率、优良率等对比，统计项目可自由选择，并生成柱状图分析。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t③.科目对比分析：分析单次考试各科目的总体情况，分析对象为科目，让教务简单直接的看到各科目的优秀率、优良率等数据，并生成分组柱状图，可以清晰的看到同一项目下例如优秀率之间的对比。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t④.教师对比分析：分析单次考试同科目的优秀率、优良率等数据，分析对象为教师。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（2）多次考试分析"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t①.学生成绩进退分析：分析考生多次考试的分数、排名等，分析对象为考生，折线图辅助分析，考生成绩趋势一目了然，还可以进行多个考生对比；"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t②.教学质量分析总表：分析对象为教师，同科目下多次考试教学质量分析，每一个科目为一个分表，每次考试可分别设置分数线；"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t③.学生偏科分析：分析学生偏科情况，以雷达图辅助，帮助教师了解学生的各科优劣，还可以分析班级的偏科情况，更加全面；"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t④.班级进退分析：班级多次考试的平均分、优秀率等项目对比，针对不同考试难度可分别设置分数线；"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t补充说明：多次考试分析只分析大考，小考不能进行多次考试分析；\n\t\t\t\t\t\t\t\t\t\t")])]),_vm._v(" "),_c('el-tab-pane',{attrs:{"label":"个人工作"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t1、发布作业：支持进行多选班级、指定班级、指定学生定时发布对应作业；支持通过短信、微信等方式提醒家长，支持查看作业列表与家长作业阅读情况反馈；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t2、教师请假：教师请假在线申请，请假审批无纸化、实时性、按权限分级审批，自动生成电子请假单；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t3、调课申请：教师发起调课申请，系统自动提醒相关教师审批。支持自动筛选出当天与调课申请人同班、不同课时、不同科目的课时以供选择；选择课时后，自动匹配科目、班级、教师信息；支持查看调课记录；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t4、意见反馈：填写意见反馈内容，如标题、照片、意见类型、意见内容可向教务反馈，在反馈记录中可以查询反馈记录；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t5、个人信息：教师可对班牌端显示的个人信息进行编辑与个人账号密码管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t6、消息中心：方便教师需要处理的工作事项的整合，以未读消息、已读消息区分，支持索引所有消息；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t7、我的评价：显示学生对该教师课程的评价与反馈；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t8、教师课堂考勤：教师可查看教师个人上课考勤情况，教务可查看全部教师上课考勤情况；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t9、用餐报名：教师可查看各时间段菜式，进行本周或下周用餐报名；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t10、公开课申请：系统提供模板，包括课题名称、教学日期、研究主题、教学类型、教案号、所属类型、年级、班级、科目、节次、教学地点、公开课级别等信息供教师操作公开课申请；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t11、公开课评价：通过标准教学评价和教学自主评价对教师公开课进行评价，开课教师可查看评价汇总；\n\t\t\t\t\t\t\t\t\t\t")])]),_vm._v(" "),_c('el-tab-pane',{attrs:{"label":"教学工作"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t1、课题资料：教师可上传或下载个人课题资料或共享课题资料；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t2、课表查询：根据权限可查看每学期教师/教室/班级/学生的排课情况，支持对不同的课表进行打印；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t3、教学计划：教师可设置自己任教课程简介；系统提供教学计划模板，包括教学年级班级、班级现状分析、特殊学生情况分析、教材分析、教学目的分析、提高教学质量的措施以及教学进度表。支持教学计划编辑、删除及提交审核操作；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t4、教师考勤：教师可查看个人本月出勤、请假、迟到、早退等数据，教务可查看所有教师月出勤情况；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t5、座位编排：包括平时座位、考试座位和考试记录。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t★（1）平时座位：首次进入时，由班主任编辑“平时座位”，通过设定行数和列数确定整体座位数量，在预览中可单个删除座位，再给学生排序，系统提供默认与随机两种排序，班主任也可拖动把学生安排到座位上，最后点击“保存方案”完成平时座位编排。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t★（2）考试座位：科任老师可以使用班主任保存的“平时座位方案”发布考试座位，也可自己重新排序。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t★（3）考试记录：通过该功能发布的“小考”的考试记录。可通过时间段、年级、班级等条件查询。补充说明：这里发布的考试会在“平时成绩”里归类为“小考”类型，考试座位会在班牌上展示。\n\t\t\t\t\t\t\t\t\t\t")])]),_vm._v(" "),_c('el-tab-pane',{attrs:{"label":"班级建设"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t1、倒计时活动：重要考试、节日、时间倒计时；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t2、班级切换：班级建设是对单个班级和智慧班牌进行设置和管理，通过该功能可对操作对象（班级）进行切换；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t3、今日德育：学校可自行设置德育考核项，如可以设置卫生、纪律、课间操、礼仪、眼保健操等；考核项不设数量限制，系统自动统计总分、年级排名和班级排名，后台公布后可在对应班牌进行查看；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t4、首页视频：班牌端首页视频的设置；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t5、班级信息：班牌端班级口号、班级宣言、班级班训、班级人数、班主任寄语、班级相册等内容的设置；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t6、班级值日：支持对一周的值日表人员进行增、删、改、查，并自动将值日表名单推送至智慧班牌；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t7、班级荣誉：支持对现有的班级荣誉进行增、删、改、查，系统至少提供2种以上荣誉图案作为底图或自行上传图片；通过荣誉模版自定义学生荣誉，采用Graphics2D将文字绘制到模版上，生成荣誉奖状图片，可直接另存为或打印制成纸质奖状；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t8、班级相册：对班牌端展示的班级相册进行管理，不同的班级班牌可设置不同的首页轮播图；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t9、班级之星：可在班级范围内选择对应的学生晋升为班级之星；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t10、作品发布：教师对班牌端展示的优秀作文、绘画、书法等作品进行管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t11、班级课表：班级课表的添加及修改管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t12、社团建设：社团的新增、解散、建设管理；\n\t\t\t\t\t\t\t\t\t\t")])]),_vm._v(" "),_c('el-tab-pane',{attrs:{"label":"教务管理"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t1、总课表：总课表的导入和下载管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t2、事务审批：教师请假、调课申请、公开课申请的审批；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t3、教师工作：教务对任课教师教学计划进行审核；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t4、走班课程管理：发布选课、学生选课、分班、排课等走班课程操作的管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t5、安全巡检：安全巡检内容、时间段设置，巡检结果的查看；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t6、课堂评价设置：教务对教师公开课评价内容的设置；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t7、行政班课堂考勤：行政班课堂考勤的查询与导出，支持按科目/年级/班级/日期进行查询；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t8、教师考勤设置：教务对教师考勤时间和考勤范围进行设置；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t9、校园导航：班牌端校园导航模块校园官网、校园广播、校园直播视频等链接设置；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t10、学生数据分析：学生男女比例，城镇与农村户口比例等基本数据分析；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t11、教师数据分析：教师男女比例等基本数据分析；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t12、课堂评价：教务可查看所有学生对所有教师的课堂评价，帮助教务进行教师水平考核；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t13、智能排课：教务智能排课功能；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t14、意见箱：可查看教师、家长、学生多方的意见汇总；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t15、今日德育设置：今日德育内容的设置与德育评分的查看、导入、导出管理；"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t16、菜谱设置：学校可以在后台录入每周的饭堂食谱，供老师订餐作为参考；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t17、社团管理：学校社团管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t18、学校风采：学校风采照片的发布与管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t19、学校荣誉：学校荣誉的编辑发布管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t20、教师风采：教师风采管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t21、班牌管理：班牌管理是控制班牌开关机设定，班级设定，禁止使用等操作的安全功能，主要达到节约用电，绿色校园的目的。具体操作如下："),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（1）未分配：对未绑定班牌的账号进行分配，管理未分配的账号；可对未分配的账号进行开关机设置，当账号分配给班级并且绑定班牌时，账号设置的开关机时间可以实时应用到班牌上，不需要再次设置；同时系统提供“自动分配”功能，大大减轻分配的工作量。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（2）已分配：对已经激活的班牌进行管理，页面展示班牌当前模式，点击可以对班牌的各项设置进行管理，如是否锁定，每天的开关机时间，还可以修改班牌账号的密码以及绑定的班级。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（3）批量设置：对已分配的班牌，能够进行一键锁定该年级班牌；可对已分配的班牌进行一键开关机设定。"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t（4）展示方式：分为两种页面展示形式列表页、图标页，用户可按照使用习惯进行选择；\n\t\t\t\t\t\t\t\t\t\t")])]),_vm._v(" "),_c('el-tab-pane',{attrs:{"label":"教材建设与供应"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t1、教具管理： 教具报修通知；\n\t\t\t\t\t\t\t\t\t\t")])]),_vm._v(" "),_c('el-tab-pane',{attrs:{"label":"基础数据管理"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t1、节次时间设置：学段、学期、节次、节次时间设置；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t2、教室管理：教学楼、教室类型等信息的录入；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t3、学籍管理：平台为每一个学生提供信息收集功能，包括基础信息：学校、年级、班级、教师等相关情况；学生的个人信息：出生年月、家长情况、居住地、户籍政治面貌、血型设置、健康状况、港澳台侨、户口类型、家庭地址等信息,以及毕业信息登记等。 "),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t★（1）学生数据导入：提供学生基本信息的维护功能，以及批量方式的学生基本信息导入，提供导入模板，并支持选择导入模板； "),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t4、教师管理：教师基础信息、任教信息管理。支持信息批量导入导出、信息变动管理、检索重置管理；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t5、年级班级管理：学段、年级、班级教室等信息的录入；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t6、科目管理：学校开设科目信息的录入，建立学校课程库；\n\t\t\t\t\t\t\t\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t\t\t\t\t\t\t\t7、考勤规则设置：学生到离校考勤时间设置；\n\t\t\t\t\t\t\t\t\t\t")])])],1),_vm._v(" "),_vm._m(3)],1)])])])}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"gree",staticStyle:{"margin-top":"50px"}},[_c('p',[_vm._v("----------教务管理系统----------")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_c('strong',[_c('span',{staticStyle:{"color":"#337FE5"}},[_vm._v("系统概述")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticStyle:{"display":"inline-block","margin-bottom":"50px","position":"relative","width":"100%"}},[_c('div',{staticStyle:{"width":"500px","margin-right":"30px"}},[_c('span',[_c('img',{attrs:{"src":require("./delete_img/oa.png"),"width":"500","title":"智慧校园教务管理系统","alt":"智慧校园教务管理系统"}})])]),_vm._v(" "),_c('div',{staticStyle:{"display":"inline-block","width":"600px","position":"absolute","top":"0","left":"510px"}},[_c('p',[_vm._v("\n\t\t\t\t\t\t\t\t\t\t智慧校园教务管理系统，帮助校园里教务处、教师、学生、家长等成员，利用计算机信息技术，高效完成日常事务；\n\t\t\t\t\t\t\t\t\t\t提供校园无纸化办公，提供信息发布，电子班牌实时数据信息。\n\t\t\t\t\t\t\t\t\t")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticStyle:{"width":"1000px"}},[_c('p',[_c('strong',[_c('span',{staticStyle:{"color":"#337FE5"}},[_vm._v("系统规格配置")])]),_c('br'),_vm._v("\n\n\t\t\t\t\t\t\t\t\t■ "),_c('strong',[_vm._v("系统配套软件")]),_vm._v(" "),_c('br'),_vm._v(" "),_c('span',[_vm._v("\n                    1）"),_c('strong',[_vm._v("移动微信端—教师端")]),_c('br'),_vm._v("\n                    1、基本信息：显示教师姓名、照片、时间日期、今日还剩课程节数等基础信息；"),_c('br'),_vm._v("\n                    2、实时数据：实时显示班级考勤、学生请假、申请结果、待我审批事项的状态，方便教师及时查看，及时处理；"),_c('br'),_vm._v("\n                    3、班级考勤：教师可查看任教班级当日考勤情况，及时了解学生到班情况；"),_c('br'),_vm._v("\n                    4、学生请假：手机在线回复处理申请，请假信息自动统计到平台，教师可轻松掌握校内学生请假出勤情况，家长和班级其他任课老师都会收到请假推送消息，其他任课教师会第一时间知道学生未出勤的原因；"),_c('br'),_vm._v("\n                    5、常用功能：教师在手机上发布作业，查看考试成绩、课程表，进行学生素质评价等操作的快捷方式；"),_c('br'),_vm._v("\n                    6、学生评价：教师可查看班级学生评价结果，并对学生个人和小组成员进行在线评价；可以查看班级学生的本周、本月评价排行榜；学生的评价会自动推送至智慧班牌、家长端，学生和老师可以进行查看；"),_c('br'),_vm._v("\n                    7、班级信息：任教教师可查看班级信息包括：班级名次、人数、班训、口号、目标、寄语、集体照等，班主任能够对班级信息进行编辑修改；"),_c('br'),_vm._v("\n                    8、请假申请：教师在线填写请假单并提交上级领导审核，审核通过后即请假成功。请假列表可查看历史请假记录；"),_c('br'),_vm._v("\n                    9、调课申请：随时随地查询本周的我的课表、班级课表。以图表的形式展示一周所上的课程、班级、课节等信息，方便教师了解本周的课时课程情况，对课程有异或调动的能够一键申请调课；"),_c('br'),_vm._v("\n                    10、公开课评价：对教师的公开课可筛选时间进行浏览，对执教者的公开课进行在线评价评分；"),_c('br'),_vm._v("\n                    11、事务审批：教务可以通过该功能处理教师提交的请假、调课、公开课等申请。也可查询记录；"),_c('br'),_vm._v("\n                    12、学校公告：根据不同的公告类型以不同图案展现，对已置顶的公告展示至最前排；展示公告详情、时间等信息；"),_c('br'),_vm._v("\n                    13、新闻动态：查看校园最新的实时动态；"),_c('br'),_vm._v("\n                    14、学校简介：学校基本信息介绍；"),_c('br'),_vm._v("\n                    15、投票：对发布的校园投票活动进行在线投票；"),_c('br'),_vm._v("\n                    16、班牌状态：查看班牌状态，可直接在微信端进行班牌的锁定和关机；"),_c('br'),_vm._v("\n                    17、意见建议：教师可随时随地发布校园意见、紧急要事、教务教学、故障报修等相关意见建议；"),_c('br'),_vm._v("\n                    18、紧急模式：教师可以直接通过微信端发布指定部分或全部班牌显示通知文字内容，提升紧急情况的传达效率；"),_c('br'),_vm._v("\n                    19、社团：以列表的方式展示社团基本信息，可查看社团的基本资料、社团简介、社团通知，管理、查询社团社员，可对申请入社的社员进行审核操作，审核通过后会成为社团正式社员；"),_c('br'),_vm._v("\n                    20、订餐：教师可查看学校食堂菜单并进行在线订餐；"),_c('br'),_vm._v("\n                    21、教师考勤：教师可以通过微信端，根据微信定位，在学校的有效范围可以直接在微信上打卡考勤；"),_c('br'),_vm._v("\n                    22、通讯录：可以任教班级学生发送消息，向家长群发消息，也可以接收学生通过班牌发送的消息；同时内置教师、家长通讯录，支持快速检索教师姓名、首字母等。展示教师姓名、任教信息、联系方式，支持一键拨通号码；"),_c('br'),_vm._v("\n                    23、我的：教师个人信息与密码管理；"),_c('br'),_vm._v(" "),_c('br'),_vm._v("\n                    2）"),_c('strong',[_vm._v("移动微信端—家长端")]),_c('br'),_vm._v("\n                    1、通知公告：学校新闻、考试、家长会等通知公告；"),_c('br'),_vm._v("\n                    2、学生留言：家长可向学生发送文字进行留言，发送后学生在班牌端登录个人中心进行查看并回复；"),_c('br'),_vm._v("\n                    3、学生考勤：家长可查看学生本月考勤情况以及当日离到校时间，及时获悉学生离到校安全情况；"),_c('br'),_vm._v("\n                    4、请假申请：家长在微信端进行请假申请，教师审批后，家长和任课教师将会收到学生请假通知及请假原因；"),_c('br'),_vm._v("\n                    5、学生成绩：家长可以查询学生成绩，及时督促学生学习。以图形、列表多种方式展示学生近期或学期考试得分情况及班排涨幅、年排涨幅、班级排名、年级排名、总分等以及本次考试的各科目分数、班级排名、联系方式；"),_c('br'),_vm._v("\n                    6、学生评价：可查看学生在班级的本周排名、本月排名，以饼状图的方式展示本周的表扬和待改进得分，以及评价记录；"),_c('br'),_vm._v("\n                    7、学生评语：查看学期内的教师评语内容包括：评语时间、评语内容、评语老师等；"),_c('br'),_vm._v("\n                    8、班级课表：学生班级课表查看；"),_c('br'),_vm._v("\n                    9、查看作业：查看最新每日各科目作业，督导孩子及时完成。家长与学生可以向老师反馈信息、意见、学生作业完成情况；"),_c('br'),_vm._v("\n                    10、社团：展示学校的社团信息，可查看社团的基本资料、简介。家长可帮助孩子在微信端进行社团申请，审核通过后，家长端可接收到社团的消息通知；"),_c('br'),_vm._v("\n                    11、作品：支持选择时间、范围、作品分类查看学校或学生个人作品观赏，并支持对优秀作品点赞；"),_c('br'),_vm._v("\n                    12、活动：了解学校或班级活动详情，帮助孩子在线报名所喜爱的活动名称或课程；"),_c('br'),_vm._v("\n                    13、投票：在线进行学校投票活动的投票，投票完成后可看到投票实时统计结果；"),_c('br'),_vm._v("\n                    14、班级相册：班级风采照片展示；"),_c('br'),_vm._v("\n                    15、班级老师：展示任教教师的简介、姓名、头像以及联系方式，轻触号码即可直接拨通教师以便家长紧急情况下联系教师；"),_c('br'),_vm._v("\n                    16、学校简介：学校简介、学校风采与荣誉资料展示；"),_c('br'),_vm._v("\n                    17、校园动态：学校新闻、动态的展示；"),_c('br'),_vm._v("\n                    18、健康体检：学生每日体检情况查看；"),_c('br'),_vm._v("\n                    19、校园菜谱：可查看学校食堂今日菜谱，并在线为学生进行订餐。1个学生为1个订餐账号，每个餐段（早、中、晚）只能订一次，如该餐段学生已在班牌端进行订餐，则家长无法再次进行订餐；"),_c('br'),_vm._v("\n                    20、学生密码：学生班牌端登录密码管理；"),_c('br'),_vm._v("\n                    21、意见建议：家长可在校提交校园意见、紧急要事、教务教学等多种意见建议；"),_c('br'),_vm._v("\n                    22、智慧学堂：通过平台为家长提供了一个学习资源库，具备校园安全、英语、国学、品德教育、科学自然等资源，满足了家长对于家庭教育的需求；"),_c('br'),_vm._v("\n                    23、消息提示：学生请假、教师评价、考试成绩、作业等信息的提示；"),_c('br'),_vm._v("\n                    24、我的信息：家长照片、姓名、联系方式的设置；"),_c('br'),_vm._v("\n                    25、解绑学生：家长可添加学生和解绑学生，若有多个孩子就读该校，可通过切换学生进行不同学生在校信息查看；"),_c('br'),_vm._v("\n                    26、修改密码：家长端登录密码修改；"),_c('br'),_vm._v("\n                    27、意见反馈：家长可提交意见反馈，该信息将直接反馈至学校教务处；"),_c('br'),_vm._v("\n                    28、家长联系人：可添加家长联系人，包括