<script>
import Axios from 'axios';
import store from '../vuex/store';
export default {
    data() {
        return {
            all_nav:[],
        }
    },
    mounted(){
        // this.textNav()
        this.dataToNav()
    },
    methods: {
        //获取导航栏数据
        dataToNav(){
            let url =`${this.$store.state.ipfg}/goodsGroup/findAll`
            Axios.get(url).then(resp=>{
                //console.log(resp)
                this.all_nav=resp.data.data
                //console.log(this.all_nav)
                for(let i=0;i<resp.data.data.length;i++){
                    this.all_nav[i].path=`/product/${this.all_nav[i].groupName}`
                    for(let j=0;j<this.all_nav[i].seconds.length;j++){
                        this.all_nav[i].seconds[j].path=`/product/${this.all_nav[i].seconds[j].groupName}`

                    }
                }
            })
        },
        //左侧菜单跳转路由
        jumphome(ids){
          let top = document.documentElement.scrollTop || document.body.scrollTop;
          // 实现滚动效果
          const timeTop = setInterval(() => {
            document.body.scrollTop = document.documentElement.scrollTop = top -= 50;
            if (top <= 0) {
              clearInterval(timeTop);
            }
          }, 10);
          // if (ids != 162) {
          this.$router.push({path:'/product_list',query:{'id':ids}});
        },
      toTop() {
        let top = document.documentElement.scrollTop || document.body.scrollTop;
        // 实现滚动效果
        const timeTop = setInterval(() => {
          document.body.scrollTop = document.documentElement.scrollTop = top -= 50;
          if (top <= 0) {
            clearInterval(timeTop);
          }
        }, 10);
      },
        //导航菜单(展开)此时需要用id去查找子项
        handleOpen(key, keyPath) {
        // console.log(key);
        // console.log(keyPath);
        
      },
      //导航菜单(闭合)
      handleClose(key, keyPath) {
        // console.log(key);
        // console.log(keyPath);
      },
      //回到product_home路由
      to_pro_home(){
          this.$router.push({path:'/product'})
      }
    },
    store
}
