<template>
    <div>
        <div class="inner">
            <div class="center">
                <!-- 主内容 -->
                    <!-- 导航栏 -->
                    <div class="nav_left">
                        <div >
                            <div>
                                <!-- 面包屑 -->
                                <!-- <div class="left_top">
                                    <el-breadcrumb separator-class="el-icon-arrow-right">
                                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                                        <el-breadcrumb-item>产品</el-breadcrumb-item>
                                    </el-breadcrumb>
                                </div> -->
                            </div>
                            <div class="nav_top">

                            </div>
                             <el-menu
                             
                                class="el-menu-vertical-demo"
                                router
                                active-text-color='#383838'
                                background-color="#F2F2F2"
                                text-color="#000"
                                @open="handleOpen"
                                @close="handleClose"
                                >
                                    <div  v-for="item in all_nav" :key="item.groupName">
                                    <!-- <el-submenu :index="item.id.toString()" v-if="true">
                                        <template slot="title">
                                        <i :class="item.icon"></i>
                                        <span>{{item.groupName}}</span>
                                        </template>
                                        <el-menu-item  v-for="val in item.seconds" :key="val.name"  @click="jumphome(val.id)">
                                        <template slot="title">
                                            <i class="icon iconfont">*</i>
                                            <span>{{val.groupName}}</span>
                                        </template>
                                        </el-menu-item>
                                    </el-submenu> -->
                                    <!-- <el-menu-item :index="item.path" v-else>
                                        <template slot="title">
                                        <i :class="item.icon"></i>
                                        <span>{{item.groupName}}</span>
                                        </template>
                                    </el-menu-item> -->

                                    <!-- 这些以后需要删掉 -->
                                    <el-submenu :index="90+'2'" v-if="true">
                                        <template slot="title">
                                        <i :class="item.icon"></i>
                                        <span>应用系统</span>
                                        </template>
                                        <a href="#/xinXiFaBu" style="text-decoration:none;">
                                        <el-menu-item >
                                        <template slot="title">
                                            <!-- <i class="icon iconfont">*</i> -->
                                            <span>信息发布系统</span>
                                        </template>
                                        </el-menu-item>
                                        </a>
                                        <a href="#/jiaoWuXiTong" style="text-decoration:none;">
                                        <el-menu-item  >
                                        <template slot="title">
                                            <!-- <i class="icon iconfont">*</i> -->
                                            <span>教务系统</span>
                                        </template>
                                        </el-menu-item>
                                        </a>
                                    </el-submenu>
                                     <!-- 这些以后需要删掉 -->


                                    </div>
                            </el-menu>
                        </div>
                    </div>
                    <div class="right_inner">
                        <router-view></router-view>
                    </div>
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            all_nav:[],
        }
    },
    mounted(){
        // this.textNav()
        this.dataToNav()
    },
    methods: {
        //获取导航栏数据
        dataToNav(){
            let url =`${this.$store.state.ipfg}/goodsGroup/findAll`
            Axios.get(url).then(resp=>{
                //console.log(resp)
                this.all_nav=resp.data.data
                //console.log(this.all_nav)
                for(let i=0;i<resp.data.data.length;i++){
                    this.all_nav[i].path=`/product/${this.all_nav[i].groupName}`
                    for(let j=0;j<this.all_nav[i].seconds.length;j++){
                        this.all_nav[i].seconds[j].path=`/product/${this.all_nav[i].seconds[j].groupName}`
                    }
                }
            })
        },
        //左侧菜单跳转路由
        jumphome(ids){
            this.$router.push({path:'/product_list',query:{'id':ids}})
        },
        //导航菜单(展开)此时需要用id去查找子项
        handleOpen(key, keyPath) {
        // console.log(key);
        // console.log(keyPath);
        
      },
      //导航菜单(闭合)
      handleClose(key, keyPath) {
        // console.log(key);
        // console.log(keyPath);
      }
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 800px;
    margin: 83px auto 0px;
    /* background: darkmagenta */
}
.inner::after{
    height: 100px;
    content: '';
    display: block;
    clear: both;
}
.center{
    position: relative;
}
 /* 左侧菜单 */
 .nav_left{
     display: inline-block;
     width: 200px;
     /* position: absolute;
     left: 20px;
     top: 20px; */
     float: left;
 }
 .right_inner{
     display: inline-block;
     /* position: absolute;
     right: 20px;
     top: 20px; */
     width: 950px;
     float: right;
 }
 .nav_top{
     height: 58px;
     /* width: 99.9%; */
     background: url(.//delete_img/ruanjianchnapin.png) no-repeat center center;
     background-size:100% auto; 
     margin-top: 50px;
 }
 .left_top{
     height: 50px;
 }

</style>



