<template>
    <div>
        <div class="inner">
            <!-- 面包屑 -->
                <div style="position: absolute">
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>招聘</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
            <div style="height:50px;"></div>
            <!-- <div class="que">
                <img src="../../assets/zhaopin/1.jpg" alt="">
                <div class="que_bot">
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;职位信息一览表</p>
                    <span>详情>></span>
                </div>
            </div> -->
            <div class="zhiwei" v-for="index in Recruitment" :key="index.i">
                <h3 class="postNames">{{index.postNames}}</h3>
                <span class="updateDates">[&nbsp;{{index.updateDates}}&nbsp;]</span>
                <hr>
                <div style="height:10px;"></div>
                <span class="workPlaces">工作地点:{{index.workPlaces}}</span>
                <br>
                <span class="experences">经验:{{index.experences}}</span>
                <span class="requires">/{{index.requires}}</span>
                <span class="mails">邮箱:{{index.mails}}</span>
                <span class="xiangqing" @click="xiangQing(index)">详情>></span>
                
                <!-- <p>{{index.qualifications}}</p>
                <p>{{index.dutys}}</p> -->
            </div>
            <div style="height:100px;"></div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../vuex/store'
export default {
    data() {
        return {
            //招聘
            Recruitment:[],
        }
    },
    created(){
        this.forMessage()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods: {
        //获取应聘职位
        forMessage(){
            let url=`${this.$store.state.ipfg}/recruitInfo/findAll/1/10`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.Recruitment=resp.data.data.content
            })
        },
        //携带参数跳转到详情
        xiangQing(val){
            console.log(val.id)
            // this.$store.state.recruitmentDetails=0
             this.$store.state.recruitmentDetails=val.id
            this.$router.push({name:'recruitmentDetails',query:{'id':val.id}})
        }
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 666px;
    margin: 83px auto 0px;
    /* background: darkmagenta */
}
.que{
    transition: 0.3s;
    position: relative;
    display: inline-block;
    width: 300px;
    height: 400px;
    /* border: 1px solid #0093DD; */
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    cursor: pointer;
}
.que>img{
    width: 100%;
    height: 100%;
}
.que>.que_bot{
    display: inline-block;
    position: absolute;
    height: 70px;
    width: 100%;
    background: rgba(225, 225, 225, .8);
    bottom: 0px;
}
.que>.que_bot>p{
    /* text-align: center; */
    line-height: 50px;
}
.que>.que_bot>span{
    position: absolute;
    bottom: 10px;
    right: 20px;
    color: #010B27;
}

.zhiwei{
    padding: 20px;
    transition: 0.3s;
    position: relative;
    display: inline-block;
    width: 500px;
    height: 150px;
    border: 1px solid #DBDDE0;
        margin: 34px;
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
.zhiwei>.postNames{
    line-height: 30px;
}
.zhiwei>.updateDates{
    color: #383838;
    position: absolute;
    right: 20px;
    top: 28px;
    font-size: 14px;
}
.zhiwei>.mails{
    position: absolute;
    bottom: 20px;
    right: 20px;
    color: #858591;
}
.zhiwei>.xiangqing{
   position: absolute;
   left: 20px;
   bottom: 20px;
   cursor: pointer;
}
.zhiwei>.xiangqing:hover{
    color: #00923F;
}
.zhiwei>.workPlaces,.zhiwei>.experences,.zhiwei>.requires{
    line-height: 30px;
    color: #858591;
}
</style>



