<template>
    <div>
        <div class="inner">
            <div class="center">
                <!-- 面包屑 -->
                <div>
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item>采购</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <div style="height:50px"></div>
                
                
                <!-- 展示的采购信息 -->
                    <div class="caiG">
                        <el-table
                        ref="singleTable"
                        :data="show_purchase"
                        highlight-current-row
                        @current-change="handleCurrentChange"
                        style="width: 100%">
                        <el-table-column
                        type="index"
                        width="50">
                        </el-table-column>
                        <el-table-column
                        show-overflow-tooltip
                        property="projectArea"
                        label="项目地区"
                        width="300">
                        </el-table-column>
                        <el-table-column
                        show-overflow-tooltip
                        property="projectName"
                        label="项目名称"
                        width="300">
                        </el-table-column>
                        <el-table-column
                        show-overflow-tooltip
                        property="projectDate"
                        label="项目开始时间"
                        width="300">
                        </el-table-column>
                        <el-table-column
                        show-overflow-tooltip
                        property="address"
                        label="地址">
                        </el-table-column>
                    </el-table>
                    </div>
                <!-- <div v-for="index in show_purchase" :key="index.z" class="card">
                    <img class="img_bg" :src="index.img" alt="">
                    <div class="bot_font">
                        <h3 class="projectName">{{index.projectName}}</h3>
                        <span class="updateDate">更新时间:{{index.updateDate}}</span>
                        <br>
                        <span class="projectArea">地区:{{index.projectArea}}</span>
                        <br>
                        <p class="tenderFile">招标信息:{{index.tenderFile}}</p>
                        <span class="xiangQing" @click="jump_purchaseDetails(index)">详情>></span>
                    </div> 
                </div> -->
                <!-- 分页 (当不需要分页时隐藏)-->
                  <div class="block" v-if="pageSize<=pageTotal">
                        <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage1"
                        :page-size="pageSize"
                        layout="total, prev, pager, next"
                        :total="pageTotal">
                        </el-pagination>
                    </div>

                <div style="height:50px"></div>
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../vuex/store';
export default {
    data() {
        return {
           //当前页
           currentPage1:1, 
           //每页显示条数
           pageSize:10,
           //总条数
           pageTotal:0,
           //展示的采购信息
        //    tableData:[],
            show_purchase:[],
            //招聘图片
            imgess:[require('../../assets/zhaopin/1.jpg'),require('../../assets/zhaopin/2.jpg'),require('../../assets/zhaopin/3.jpg'),require('../../assets/zhaopin/4.jpg'),require('../../assets/zhaopin/5.jpg'),require('../../assets/zhaopin/6.jpg'),require('../../assets/zhaopin/7.jpg')],
            
        }
    },
    created(){
        this.for_purchase()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods:{
        //获取采购信息
        for_purchase(){
            let url=`${this.$store.state.ipfg}/project/findAll/${this.currentPage1}/${this.pageSize}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.show_purchase=resp.data.data.content
                this.pageTotal=resp.data.data.totalPages
                for(let j=0;j<this.show_purchase.length;j++){
                    if(this.show_purchase.length<this.imgess.length){
                        this.show_purchase[j].img=this.imgess[j]
                    }else if(this.show_purchase.length>this.imgess.length){
                        if(j<=this.imgess.length){
                            this.show_purchase[j].img=this.imgess[j]
                        }else{
                            this.show_purchase[j].img=this.imgess[j%7]
                        }
                    }
                        
                    
                }
                console.log(this.show_purchase)
            })
        },
        //跳转到详情页面(/jump_purchaseDetails.vue)
        // jump_purchaseDetails(val){
        //     console.log(val)
        //     this.$router.push({name:'purchaseDetails',query:{'data':val.id}})
        // },
        //分页
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      //表格
       handleCurrentChange(val) {
        this.currentRow = val;
        console.log(this.currentRow)
        this.$router.push({name:'purchaseDetails',query:{'data':val.id}})
      }
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 800px;
    margin: 83px auto 0px;
    /* background: darkmagenta */
}

.center{
    position: relative;
    padding: 20px 40px;
}
/* 采购信息 */
.caiG{
    cursor: pointer;
}
 /* .card{
     margin: 15px;
     display: inline-block;
     width: 300px;
     height: 400px;
     border-radius:5px; 
     box-shadow: 0 0px 12px 0 rgba(0,0,0,.1);
     transition: 0.3s;
     position: relative;
 }
 .card:hover{
     box-shadow: 10px 10px 30px 10px rgba(0,0,0,.2);
     transition: 0.3s;
 }
 .card>.bot_font{
     display: inline-block;
     height: 100px;
     width: 260px;
    position: absolute;
    bottom: 0;
    background: rgba(255,255,255,.7);
    padding: 20px;
 }
 .card>.img_bg{
     width: 100%;
     height: 100%;
 }
 .card>.bot_font>.xiangQing{
     position: absolute;
     right: 20px;
     cursor: pointer;
     font-size:15px;
 }
 .card>.bot_font>.xiangQing:hover{
     color: #00923F;
 } */
</style>



