import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/pages/home'
import eetown from '@/components/pages/eetown'
import et_news from '@/components/pages/et_news'
import lookNews from '@/components/pages/lookNews'
import industry_news from '@/components/pages/industry_news'
import industry from '@/components/pages/industry'
import product  from '@/components/pages/product'
import recruit  from '@/components/pages/recruit'
import recruitmentDetails  from '@/components/pages/recruitmentDetails'
import uploadResume  from '@/components/pages/uploadResume'
import purchase  from '@/components/pages/purchase'
import blockchain  from '@/components/pages/blockchain'
import purchaseDetails  from '@/components/pages/purchaseDetails'

// 产品文件夹
import product_home  from '@/components/pages/products/product_home'
import product_list  from '@/components/pages/products/product_list'
import product_details  from '@/components/pages/products/product_details'
import ccc from '@/components/pages/ready_delete/ccc'//3c认证

//解决方案
import smartCampusSolutions  from '@/components/pages/smartCampusSolutions'
import smartCitySolutions  from '@/components/pages/smartCitySolutions'
import smartVillageSolutions from '@/components/pages/smartVillageSolutions'
import networkSolutions from '@/components/pages/networkSolutions'

//这些是临时页面 -软件产品soft 到时候删掉
import irs_title  from '@/components/pages/ready_delete/irs_title'
import eas_title  from '@/components/pages/ready_delete/eas_title'
import wareless  from '@/components/pages/ready_delete/wareless'
import soft  from '@/components/pages/ready_delete/soft'
import soft_home from '@/components/pages/ready_delete/soft_home'


import iph_class from '@/components/pages/ready_delete/iph_class'//信息发布主机类
import ecb_class from '@/components/pages/ready_delete/ecb_class'//电子班牌类
import lcd_class from '@/components/pages/ready_delete/lcd_class'//lcd类
import informationPublishingHost from '@/components/pages/ready_delete/informationPublishingHost'//信息发布主机型号
import electronicClassBrand from '@/components/pages/ready_delete/electronicClassBrand'//电子班牌
import LCDscreen from '@/components/pages/ready_delete/LCDscreen'//lcd屏

Vue.use(Router)
export default new Router({
  routes: [
    //首页
    {
      path: '/',
      name: 'home',
      component: home,
    },
    //意通新闻
    {
      path:'/et_news',
      name:'et_news',
      component: et_news,
    },
    //意通新闻_查看新闻
    {
      path:'/lookNews',
      name: 'lookNews',
      component: lookNews
    },
    //行业新闻
    {
      path:'/industry_news',
      name:'industry_news',
      component:industry_news
    },
    //行业新闻_查看新闻
    {
      path:'/industry',
      name:'industry',
      component:industry,
    }, 
    //产品
    {
      path:'/product',
      name:'product',
      component:product,
      children:[
        //产品主页
        {
          path:'/',
          name:'product_home',
          component:product_home
        },
        //产品列表
        {
          path:'/product_list',
          name:'product_list',
          component:product_list
        },
        //产品详情
        {
          path:'/product_details',
          name:'product_details',
          component:product_details
        },
        //______此处以后要删除_________
        {
          path:'/iph_class',//信息发布主机类
          name:'iph_class',
          component:iph_class,
        },
        {
          path:'/ecb_class',//电子班牌类
          name:'ecb_class',
          component:ecb_class,
        },
        {
          path:'/lcd_class',//lcd类
          name:'lcd_class',
          component:lcd_class,
        },
    {
      path:'/irs_title',
      name:'irs_title',
      component:irs_title
    },
    {
      path:'/eas_title',
      name:'eas_title',
      component:eas_title
    },
    {
      path:'/wareless',
      name:'wareless',
      component:wareless
    },
    {
      path:'/informationPublishingHost',//信息发布主机型号
      name:'informationPublishingHost',
      component:informationPublishingHost
    },
    {
      path:'/electronicClassBrand',//电子班牌
      name:'electronicClassBrand',
      component:electronicClassBrand
    },
    {
      path:'/LCDscreen',//lcd屏
      name:'LCDscreen',
      component:LCDscreen
    },
    {
      path:'/ccc',
      name:'ccc',
      component:ccc,
    },
        //______此处以后要删除_________
      ]
    },
   //软件产品..............这个以后删掉2...................
   {
    path:'/soft',
    name:'soft',
    component:soft,
    children:[
      {
        path:'/',
        name:'soft_home',
        component:soft_home
      },
       
    ]
   },

   //软件产品..............这个以后删掉2...................


   //智慧校园解决方案
   {
    path:'/smartCampusSolutions',
    name:'smartCampusSolutions',
    component:smartCampusSolutions,
   },
   //智慧乡村解决方案
   {
    path:'/smartVillageSolutions',
    name:'smartVillageSolutions',
    component:smartVillageSolutions,
   },
   //智慧城市解决方案
   {
     path:'/smartCitySolutions',
     name:'smartCitySolutions',
    component:smartCitySolutions,
   },
   //全光网解决方案
   {
     path:'/networkSolutions',
     name:'networkSolutions',
     component:networkSolutions,
   },
    //招聘
    {
      path:'/recruit',
      name:'recruit',
      component:recruit,
    },
    //招聘详情
    {
      path:'/recruitmentDetails',
      name:'recruitmentDetails',
      component:recruitmentDetails
    },
    //上传简历
    {
      path:'/uploadResume',
      name:'uploadResume',
      component:uploadResume
    },
    //采购
    {
      path:'/purchase',
      name:'purchase',
      component:purchase,
    },
    {
      path:'/blockchain',
      name:'blockchain',
      component:blockchain,
    },
    //采购详情
    {
      path:'/purchaseDetails',
      name:'purchaseDetails',
      component:purchaseDetails
    },
    //关于意通
    {
      path:'/eetown',
      name: 'eetown',
      component: eetown,
    }
  ]
})



