<template>
    <div>
        <div>
            <h3 class="pru">产品列表</h3>
            <hr>
           <div class="lengthways">
               <div class="img_size">
                   <img src="./delete_img/lcdping.png" alt="" @click="jup">
                   </div>
                    <div class="font"><p><span class="title" @click="jup">LCD屏</span>
                     <span class="particular" @click="jup">详细信息</span>
                     </p> 
                     <p class="introduction">
                         <span>产品概述:</span>
                          <span>
                        互动展板	
                        屏体类型	LED背光源显示屏
                        屏体质量	A级硬屏
                        显示比例	16:9
                        显示尺寸	55英寸
                        可视角度	178°
                        前置2*15W扬声器，保证扩声音质	符合
                        物理解析度	1920*1080
                        前置输入接口	提供至少1 路HDMI 高清输入接口(标准HDMI 接口，不接受转接方式)，满足高清信号源输入需求
                        后置输入接口	USB多媒体*2、USB触控*1、HDMI*2、、RJ45*1、音频*1、TV*1、AV*1、 RS232*1、VGA*1
                        触摸面材质	钢化玻璃硬度不低于7H,可见光透射比≥89%,雾度范围2%-5%
                        感应方式	红外触摸感应
                        技术特性	手指或书写笔，HID免驱
                        触摸、书写点数	十点触摸、十点书写
                        触摸分辨率	32768*32768
                        定位精度	0.1mm
                        通讯接口	USB
                        安卓系统功能	（1）、画面冻结：任意通道对画面进行冻结，并可实现对冻结画面的放大，漫游（缩小，放大时画面可移动）；
                        （2）、具备智能温控系统，有效监控、预警和断电保护功能，无 PC 状态下，实时显示屏体温度，并可根据温度变化显示不同颜色进行提示；
                </span></p></div></div>
            
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //纵向列表
            list:[]
        }
    },
    watch: {
        $route(){
            //路由参数改变页面重新渲染
            this.forListData()
        }
    },
    created(){
        this.forListData()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods: {
        //获取产品列表
        forListData(){
            this.list=[]
            let url=`${this.$store.state.ipfg}/goods/condition/${this.$route.query.id}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.list=resp.data.data
            })
        },
        //携带参数到产品详情页面
        // topro(index){
        //     // alert(index.id)
        //     this.$router.push({path:'/product_details',query:{'id':index.id}})
        //     let url=`${this.$store.state.ipfg}/goods/hot/${index.id}`
        //     console.log(url)
        //     Axios.get(url).then(resp=>{
        //         console.log(resp)
        //     })
        // }
        jup(){
            this.$router.push({path:'/LCDscreen'})
        }
    },
    store
}
</script>
<style scoped>

 .bg_top{
    height: 60px;
    /* background: url(../../../assets/topimg/topimg.png) no-repeat center center; */
    background-size:100%; 
}
.pru{
    font-size: 16px;
    color: #333333;
    line-height: 45px;
}
 /* 列表插图 */
 .lengthways{
    height: 170px;
    margin: 20px 0px;
    background: #FBFBFB;
    position: relative;
}
.lengthways>.img_size{
    display: inline-block;
    cursor: pointer;
}
 .lengthways>.img_size>img{
     border: 1px solid #CCCCCC;
     display: inline-block;
     width: 230px;
     height: 150px;
     margin: 9px;
 }
 .lengthways>.font{
     background: ccc;
     position: absolute;
     right: 0px;
     height: 130px;
     top: 0px;
     width: 660px;
     padding: 20px;
     overflow: hidden;
     /* border: 1px solid gold; */
 }
.lengthways>.font>p>.title{
    color: #4b8ada;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
    cursor: pointer;
}
.lengthways>.font>p>.particular{
    text-align: center;
    line-height: 20px;
    width: 80px;
    display: block;
    background: #4b8ada;
    height: 20px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}
.lengthways>.font>.introduction{
    font-size: 13px;
    line-height: 25px;
}
</style>



