<template>
    <div>
        <div>
            <h3 class="pru">产品列表</h3>
            <hr>
            <div v-for="(item) in list" :key="item.c" class="lengthways">
                <div class="img_size"  @click="topro(item)">
                    <img :src="item.image2" alt="">
                </div>
                <div class="font">
                    <p>
                        <span class="title" @click="topro(item)">{{item.goodsName}} · 型号{{item.caption}}</span>
                        <span class="particular"  @click="topro(item)">详细信息</span>
                    </p>
                    <p class="introduction">
                        <span>产品概述:</span>
                        <span>{{item.introduction}}</span>
                    </p>
                </div>
            </div>
            
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //纵向列表
            list:[]
        }
    },
    watch: {
        $route(){
            //路由参数改变页面重新渲染
            this.forListData()
        }
    },
    created(){
        this.forListData()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods: {
        //获取产品列表
        forListData(){
            this.list=[]
            let url=`${this.$store.state.ipfg}/goods/condition/${this.$route.query.id}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.list=resp.data.data
            })
        },
        //携带参数到产品详情页面
        topro(index){

           //无线覆盖系统
          if (index.id == 116) {
            this.$router.push({path: '/wareless', query: {'id': index.id}});
            return;
          }
          //应用软件
          if (index.id == 120) {
            this.$router.push({path: '/irs_title', query: {'id': index.id}});
            return;
          }
          if (index.id == 121) {
            this.$router.push({path: '/eas_title', query: {'id': index.id}});
            return;
          }

            this.$router.push({path:'/product_details',query:{'id':index.id}});
            let url=`${this.$store.state.ipfg}/goods/hot/${index.id}`
            console.log(url)
            Axios.get(url).then(resp=>{
                console.log(resp)
            })
        }
    },
    store
}
</script>
<style scoped>

 .bg_top{
    height: 60px;
    /* background: url(../../../assets/topimg/topimg.png) no-repeat center center; */
    background-size:100%; 
}
.pru{
    font-size: 16px;
    color: #333333;
    line-height: 45px;
}
 /* 列表插图 */
 .lengthways{
    height: 170px;
    margin: 20px 0px;
    background: #FBFBFB;
    position: relative;
}
.lengthways>.img_size{
    display: inline-block;
    cursor: pointer;
}
 .lengthways>.img_size>img{
     border: 1px solid #CCCCCC;
     display: inline-block;
     width: 230px;
     height: 150px;
     margin: 9px;
 }
 .lengthways>.font{
     background: ccc;
     position: absolute;
     right: 0px;
     height: 130px;
     top: 0px;
     width: 660px;
     padding: 20px;
     overflow: hidden;
     /* border: 1px solid gold; */
 }
.lengthways>.font>p>.title{
    color: #4b8ada;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
    cursor: pointer;
}
.lengthways>.font>p>.particular{
    text-align: center;
    line-height: 20px;
    width: 80px;
    display: block;
    background: #4b8ada;
    height: 20px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}
.lengthways>.font>.introduction{
    font-size: 13px;
    line-height: 25px;
}
</style>



