<template>
<div>
    <div  class="inner">
        <div class="center">
            <!-- 正文 -->
            <!-- 轮播图 -->
            <div class="block">
                <el-carousel trigger="click" height="400px">
                <el-carousel-item v-for="item in 4" :key="item">      
                </el-carousel-item>
                </el-carousel>
            </div>
            <!-- 热销产品 -->
            <div class="main_font">
                <h3>----------&nbsp;&nbsp;&nbsp;热销产品&nbsp;&nbsp;&nbsp;----------</h3> 
            </div >
            <div class="zzc">
                <div class="hei">
               
                    <div class="card" v-for="(item) in hots" :key="item.f" @click="findGoods(item)">
                        <div class="dc_img">
                            <img :src="item.image2" alt="">
                        </div>
                        <div class="dc_hot_font">
                            <p>
                              <span>{{item.goodsName}}</span>
                            </p>
                            <p>
                                <span>型号:{{item.caption}}</span>
                            </p>
                        </div>
                    </div>  
                </div>
            </div>
            
            <!-- 主营业务 -->
            <!-- <div class="main_font">
                <h3>----------&nbsp;&nbsp;&nbsp;主营业务&nbsp;&nbsp;&nbsp;----------</h3> 
            </div>
            <div class="main_cd_up"> 
                <div><h3>新产品研发</h3></div>
                <div><h3>系统集成</h3></div>
                <div><h3>软件开发</h3></div>
            </div>
            <div class="main_cd_down">
                <div><h3>技术服务</h3></div>
                <div><h3>专业咨询服务</h3></div>
            </div> -->

            <!-- 主要新闻 -->
            <div class="main_font">
                <h3>----------&nbsp;&nbsp;&nbsp;意通新闻&nbsp;&nbsp;&nbsp;----------</h3> 
            </div>
            <div style="height:50px;"></div>
                <div>
                    <div style="font-size:0px;">
                        <div class="squ" v-for="(index) in news" :key="index.s" @click="look(index)">
                            <img  :src="index.image" alt="" class="toImg">
                            <div class="bot_font">
                                <p>&nbsp;&nbsp;&nbsp;&nbsp;{{index.title}}</p>
                                 <a class="new_tittle" >详细内容>>&nbsp;&nbsp;&nbsp;&nbsp;</a>
                            </div>
                        </div>
                    </div>
                </div>

                 <div class="main_font">
                    <h3>----------&nbsp;&nbsp;&nbsp;行业新闻&nbsp;&nbsp;&nbsp;----------</h3> 
                </div>
                <div style="height:50px;"></div>
                <div>
                    <div style="font-size:0px;">
                        <div class="squ" v-for="(index) in news2" :key="index.s" @click="look2(index)">
                            <img  :src="index.image" alt="" class="toImg">
                            <div class="bot_font">
                                <p>&nbsp;&nbsp;&nbsp;&nbsp;{{index.title}}</p>
                                 <a class="new_tittle" >详细内容>>&nbsp;&nbsp;&nbsp;&nbsp;</a>
                            </div>
                        </div>
                    </div>
                </div>

            <div style="height:100px;"></div>
        </div>
    </div>
</div>
    
</template>
<script>
import echarts from 'echarts'
import Axios from 'axios';
import store from '../vuex/store';
export default {
    data() {
        return {
            //图表
            dataList:[
               {value:32, name:'技术员32%'},
               {value:28, name:'工程师28%'},
               {value:20, name:'高级工程师20%'},
               {value:20, name:'管理人员20%'},
            //    {value:3, name:'81-100分'},
            ],
            //卡片
            currentDate: new Date(),
            //产品中心
            product_center:[],
            //热门产品
            hots:[],
            //from
             dynamicValidateForm: {
          domains: [{
            value: ''
          }],
          email: ''
        },
        //意通新闻
            news:[],
        //行业新闻
            news2:[],

        }
    },
    created(){
        this.forprod()
        this.forhot()
        this.forNews()
        this.forNews2()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods:{
         // 图表初始化数据
        initChart(){
            let myChart = echarts.init(this.$refs.myEchart);
            myChart.setOption({
                tooltip: {
                    trigger:'item',
                    formatter: "{a} <br/>{b}: {c} <br/> ({d}%)"
                },
                series : [
                    {
                        name:'深圳市意通信息技术有限公司',
                        type: 'pie',
                        radius : '80%',
                        center: ['52%', '53%'],
                        avoidLabelOverlap: false,
                        data: this.dataList,
                        itemStyle: {
                            emphasis: {
                                shadowBlur: 5,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.3)'
                            }
                        }
                    }
                ]
            })
        },
        //获取产品
        forprod(){
            let url =`${this.$store.state.ipfg}/goodsGroup/findAll`
            Axios.get(url).then(resp=>{
                console.log(resp)
            })
        },
        //读取热门产品
        forhot(){
            let url=`${this.$store.state.ipfg}/goods/hot`
            Axios.get(url).then(resp=>{
                console.log('\\\\\\edrere')
                console.log(resp)
                this.hots=resp.data.data
            })
        },
        //点击查找,跳转热门商品
        findGoods(item){
            console.log(item.id)
            this.$router.push({path:'/product_details',query:{'id':item.id}})
        },
        //获取意通新闻
        forNews(){
            let url =`${this.$store.state.ipfg}/companyNews/findAll/1/5`
            // console.log(url)
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.news=resp.data.data.content
                
            })
        },
        //获取行业新闻
        forNews2(){
            let url =`${this.$store.state.ipfg}/industryNews/findAll/1/5`
            // console.log(url)
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.news2=resp.data.data.content
                
            })
        },
        //查看意通新闻
        look(value){
             this.$router.push({name:'lookNews',query:{'val':value.id}})
        },
        //查看行业新闻
         look2(value){
            console.log(value)
            this.$router.push({name:'industry',query:{'val':value.id}})
        }
    },
     mounted () {
        let obj = this.$refs.myEchart;
        if(obj){
            this.initChart();
        }
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    /* height: 2000px; */
    margin: 83px auto 0px;
    /* background: darkmagenta */
}
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
    height: 100%;
  }
  .el-carousel__item:nth-child(3) {
     background: url('../../assets/lunbotu/2.jpg') no-repeat;
     background-size:100% ; 
  }
  .el-carousel__item:nth-child(5) {
      background: url('../../assets/lunbotu/1.png') no-repeat;
     background-size:100% ;
  }
    .el-carousel__item:nth-child(6) {
     background: url('../../assets/lunbotu/3.png') no-repeat;
     background-size:100% ; 
  }
  .el-carousel__item:nth-child(4) {
      background: url('../../assets/lunbotu/4.png') no-repeat;
     background-size:100% ;
  }
 
/* 热销产品 */
.zzc{
    padding: 10px 0px;
    width: 99%;
    overflow: hidden;
    background: none;
}
.hei{
    width: 24000000px;
    /* overflow: hidden; */
    /* border: 1px solid; */
}
 .card{
     margin: 16px;
     display: inline-block;
     width: 270px;
     height: 300px;
     border-radius:5px; 
     box-shadow: 0 0px 12px 0 rgba(0,0,0,.1);
     transition: 0.3s;
     cursor: pointer;
 }
 .card:hover{
     box-shadow: 4px 4px 10px 4px rgba(0,0,0,.2);
      /* box-shadow: 5px 16px 18px 0 rgba(0,0,0,.1); */
     transition: 0.3s;
 }
.card>.dc_img{
    width: 100%;
    height: 200px;
}
.card>.dc_img>img{
    width: 100%;
}
.card>.dc_hot_font{
    margin: 30px;
}
 /* 主营业务 */
  .main_cd_down,.main_cd_up{
      display: flex;
  }
  .main_cd_down>div{
      position: relative;
       flex: 1;
      height: 295px;
  }
  .main_cd_up>div{
      position: relative;
      flex: 1;
      height: 197px;
  }
  .main_cd_up>div:after,.main_cd_down>div:after{
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background-color: rgba(255, 255, 255, .5);
    /* opacity: 0.3; */
    z-index: 1;
    width: 100%;
    height: 100%;
  }
 .main_cd_up>div:nth-child(1){
      background: url('../../assets/maindc/me1.png') no-repeat;
      background-size: 110% auto; 
      background-position:center center;
      transition: 0.3s;
  } 
.main_cd_up>div:nth-child(2){
      background: url('../../assets/maindc/me2.png') no-repeat;
      background-size: 110% auto; 
      background-position:center center;
      transition: 0.3s;
  } 
.main_cd_up>div:nth-child(3){
      background: url('../../assets/maindc/me3.png') no-repeat;
      background-size: 110% auto; 
      background-position:center center;
      transition: 0.3s;
  } 
    .main_font{
        margin-top: 50px;
        height: 100px;
        line-height: 100px;
        /* border-bottom:1px solid #737476; */
    }
    .main_font>h3{
        text-align: center;
        color: #737476;
        font-size: 25px;
    }
.main_cd_down>div:nth-child(1){
    background: url('../../assets/maindc/me4.png') no-repeat;
    background-size: 110% auto; 
      background-position:center center;
      transition: 0.3s;
}
.main_cd_down>div:nth-child(2){
    background: url('../../assets/maindc/me5.png') no-repeat;
    background-size: 110% auto; 
      background-position:center center;
      transition: 0.3s; 
}

.main_cd_up>div:hover,.main_cd_down>div:hover{
    background-size: 100% auto; 
      background-position:center center;
      transition: 0.3s; 
}
.main_cd_up>div:hover::after,.main_cd_down>div:hover::after{
    transition: 0.3s;
    background-color: rgba(255, 255, 255, 0);
}
.main_cd_up>div:hover h3,.main_cd_down>div:hover h3{
    transition: 0.3s;
    color: transparent;
    background-color: #fff;
    text-shadow: rgba(255,255,255,1) 0 15px 16px, rgba(255,255,255,1) 10px 13px 13px;
    -webkit-background-clip: text;
    -webkit-text-stroke: 1.5px #000;
    font-weight: 700;
    -webkit-transition: 0.3s;
}
.main_cd_up>div>h3,.main_cd_down>div>h3{
    font-size: 22px;
    display: inline-block;
    width: 140px;
    height: 30px;
    /* color: #fff; */
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 2;
}
/*人员结构 图表 */
.echart{
    width: 800px;
    margin: auto;
    position: relative;
    /* border: 1px solid #E98F6F; */
    /* background: firebrick; */
}
/* 服务理念 */
.fuln{
    height: 500px;
}
.echart>.fang:hover{
    transition: 0.3s;
    width: 350px;
    height: 230px;
}
.echart>.yuan:hover{
    transition: 0.3s;
    width: 230px;
    height: 230px;
}
.echart>.fang{
    transition: 0.3s;
    display: inline-block;
    width: 300px;
    height: 200px;
    border-radius: 10px;
    /* border: 1px solid gold; */
    -webkit-box-shadow: 0px 2px 1px #252526;
    box-shadow: -5px 12px 20px #252526;
}


.echart>.yuan{
    transition: 0.3s;
    display: inline-block;
    width: 200px;
    height: 200px;
    border-radius:50%;
    border: 50px solid #fff; 
        -webkit-box-shadow: 0px 2px 1px #252526;
    box-shadow: -5px 12px 20px #252526;
}
.echart>.fang:nth-child(1){
    background: #F69C19;
    position: absolute;
    top:20px;
    left: 20px;
}
.echart>.fang:nth-child(2){
    background: #D22137;
    position: absolute;
    right:20px;
    top: 20px;
}
.echart>.fang:nth-child(3){
    background: #0B8136;
    position: absolute;
    left:20px;
    bottom: 20px;
}
.echart>.fang:nth-child(4){
    background: #1896C8;
    position: absolute;
    right:20px;
    bottom: 20px;
}
.echart>.yuan{
    background: #DC4743;
    position: absolute;
    left: 0;
    right: 0;
    top:0;
    bottom: 0;
    margin: auto;
}
.echart>.yuan>div{
    font-size: 40px;
    display: inline-block;
    width: 92px;
    height: 100px;
    /* border: #0B8136 solid 1px; */
    color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}
.echart>.fang>div{
    font-size: 30px;
    display: inline-block;
    width: 120px;
    height: 50px;
    /* border: #0B8136 solid 1px; */
    color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}
/* 服务领域 */
.fwly{
    /* position: relative; */
}
.fwly>div{
    transition: 0.3s;
    position: absolute;
    display: inline-block;
    width: 300px;
    height: 80px;
    /* border: 1px solid gold; */
    border-radius:8px; 
    -webkit-box-shadow: 0px 2px 1px #252526;
    box-shadow: -5px 12px 20px #252526;
}
.fwly>div>div{
    font-size: 20px;
    display: inline-block;
    width: 80px;
    height: 30px;
    /* border: #0B8136 solid 1px; */
    color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}
.fwly>div:hover{
    transition: 0.3s;
     width: 320px;
    height: 90px;
}
.fwly>div:nth-child(1){
    background: #0B8136;
    /* position: absolute; */
    top: 20px;
    left: 20px;
}
.fwly>div:nth-child(2){
    background: #199ACD;
    top: 120px;
    left: 20px;
}
.fwly>div:nth-child(3){
    background: #D52238;
    top: 220px;
    left: 20px;
}
.fwly>div:nth-child(4){
    background: #F69C19;
    top: 320px;
    left: 20px;
}
.fwly>div:nth-child(5){
    background: #199ACD;
    top: 20px;
    right: 20px;
}
.fwly>div:nth-child(6){
    background: #D52238;
    top: 120px;
    right: 20px;
}
.fwly>div:nth-child(7){
    background: #F69C19;
    top: 220px;
    right: 20px;
}
.fwly>div:nth-child(8){
    background: #0B8136;
    top: 320px;
    right: 20px;
}
 .squ{
        position: relative;
        overflow: hidden;
        cursor: pointer;  
        font-size: 16px;
        transition: 0.3s;
    }

    .squ:nth-child(1),.squ:nth-child(2){
        display: inline-block;
        width: 600px;
        height: 300px;
        margin: 0;
        border: 1px solid  rgba(225, 225, 225, .8);
    }
    .squ:nth-child(3),.squ:nth-child(4),.squ:nth-child(5){
        display: inline-block;
        width: 400px;
        height: 300px;
        margin: 0;
        border: 1px solid  rgba(225, 225, 225, .8);
    }
    .squ>.bot_font{
        height: 50px;
        width: 100%;
        background: rgba(225, 225, 225, .5);
        position: absolute;
        bottom: 0;  
    }
    .squ>.bot_font>.new_tittle{
        text-decoration: none;
        /* color: #000; */
        position: absolute;
        right: 0;
    }
    .squ>.toImg{
        width: 100%;
        height: 100%;
    }

</style>



