<template>
    <div>
        <div class="inner">
            <div class="center">
                <div style="height:50px"></div>
                <!-- 面包屑 -->
                <div>
                    <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path: '/purchase' }">采购</el-breadcrumb-item>
                        <el-breadcrumb-item>{{purchaseDetails.projectName}}</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <div class="purchaseDetails">
                    <h3 class="projectName">{{purchaseDetails.projectName}}</h3>
                    <p class="fu">
                        <span class="projectDate">发布时间:{{purchaseDetails.projectDate}}</span>
                        <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
                        <span class="tenderReference">招标编号:{{purchaseDetails.tenderReference}}</span>
                    </p>
                    <hr>
                    <div class="fu2">
                        <p>招标单位:{{purchaseDetails.tenderer}}</p>
                        <p>地区:{{purchaseDetails.projectArea}}</p>
                        <p>截止时间:{{purchaseDetails.deadline}}</p>
                        <p>报名地址:{{purchaseDetails.address}}</p>
                        <p>代理机构:{{purchaseDetails.agency}}</p>
                        <p>详情细则:{{purchaseDetails.tenderFile}}</p>
                        <p>关键信息:{{purchaseDetails.keyInfo}}</p>
                         <p>联系人:{{purchaseDetails.linkerman}}</p>
                         <p>联系电话:{{purchaseDetails.tel}}</p>
                         <p>电子邮箱:{{purchaseDetails.mail}}</p>
                         <p>招标人:{{purchaseDetails.tenderMan}}</p>
                         <p>中标单位:{{purchaseDetails.winningBidder}}</p>
                         <p>评审结果:{{purchaseDetails.assess}}</p>
                    </div>
                    
                </div>
                <div style="height:50px"></div>
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../vuex/store';
export default {
    data() {
        return {
            //内容
            purchaseDetails:{},
        }
    },
    created(){
        this.for_purchaseDetails()
    },
    methods:{
        //获取采购信息
        for_purchaseDetails(){
            let url=`${this.$store.state.ipfg}/project/findOne/${this.$route.query.data}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.purchaseDetails=resp.data.data
                console
            })
        }
    },
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 800px;
    margin: 0px auto 0px;
    /* background: darkmagenta */
    /* border: #383838 1px solid; */
    padding: 50px;
}

.center{
    position: relative;
}
.purchaseDetails{
    position: relative;
}
.purchaseDetails>.fu{
    text-align: center;
    line-height: 30px;
}
.purchaseDetails>.fu2{
    line-height: 25px;
}
.purchaseDetails>.projectName{
    text-align: center;
    line-height: 50px;
}
.purchaseDetails>.fu>.projectDate{
    font-size: 14px;
    color: #383838;
}
.purchaseDetails>.fu>.tenderReference{
    font-size: 14px;
    color: #383838;
}
</style>



