<template>
    <div>
        <div class="inner">
            <div class="center">
                <h3>报名</h3>
                <hr>
                <div class="font">
                    <p>
                        <span>* 电话号码:</span>
                        <span class="tel"><el-input v-model="Resume.tel" placeholder="请输入内容"></el-input></span>
                    </p>
                    <p>
                        <span>* 电子邮箱:</span>
                        <span class="mail"><el-input
                        v-model="Resume.mail" placeholder="请输入内容"></el-input></span>
                    </p>
                    <!-- <p>
                        <span class="zhan"></span>
                        <span style="display:inline-block;">
                            <el-upload
                            class="upload-demo"
                            :action="`${this.$store.state.ipfg}/file/upload`"
                            multiple
                            :limit="1"
                            :on-success="text_s"
                            :file-list="fileList">
                            <el-button  type="primary" disabled>点击上传简历</el-button>
                        </el-upload>
                        </span>
                    </p> -->
                    <p>
                        <span>* 留言:</span>
                        <span class="remarks">
                            <el-input
                                type="textarea"
                                :rows="4"
                                placeholder="请输入内容"
                                v-model="Resume.remarks">
                                </el-input>
                        </span>
                    </p>
                    <p>
                        <span class="zhan"></span>
                        <span><el-button type="primary" @click="submit_s">&nbsp;&nbsp;确&nbsp;&nbsp;&nbsp;&nbsp;定&nbsp;&nbsp;</el-button></span>
                        <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
                        <span><el-button type="danger">&nbsp;&nbsp;取&nbsp;&nbsp;&nbsp;&nbsp;消&nbsp;&nbsp;</el-button></span>
                    </p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../vuex/store';
export default {
    data() {
        return {
            //简历信息
           Resume:{
               tel: '',
               mail:'',
               src:'',
               remarks:'',
           } ,
            fileList:[],
            //from
        }
    },
    created(){
        
    },
    methods: {
        //上传至文件服务器
      text_s(response){
          console.log(response.data)
          this.Resume.src=response.data
      },
      //确定
      submit_s(){
          let url=`${this.$store.state.ipfg}/hunter/add`
          let data=this.Resume
          Axios.post(url,data).then(resp=>{
              console.log(resp)
              this.Resume={
               tel: '',
               mail:'',
               src:'',
               remarks:'',
           }
           this.succeed_s()
          })
      },
      //上传成功
      succeed_s() {
        this.$message({
          message: '上传成功,敬请期待!',
          type: 'success'
        });
      },

    },
    
    store
}
</script>
<style scoped>
.inner{
    width: 1220px;
    min-height: 800px;
    margin: 83px auto 0px;
    /* background: darkmagenta */
    /* border: #383838 1px solid; */
    padding: 50px;
}
.font{
    /* text-align: center; */
    /* display: inline-block; */
    /* border: 1px saddlebrown solid; */
    line-height: 70px;
    width: 600px;
    margin: auto;
}
.center{
    position: relative;
}
.tel,.mail{
    display: inline-block;
    width: 513px;
}
.zhan{
    display: inline-block;
    width: 85px;
}
</style>



