<template>
    <div>
        <div>
            <h3 class="pru">产品列表</h3>
            <hr>
            <div   class="lengthways">
                <div   class="img_size">
                    <img   src="./delete_img/dianzibanpai.png" alt="" @click="jup">
                    </div>
                     <div   class="font">
                         <p  >
                             <span   class="title" @click="jup">电子班牌</span>
                              <span   class="particular" @click="jup">详细信息</span>
                              </p>
                               <p   class="introduction">
                                   <span  >产品概述:</span> 
                                   <span  >
                        电子班牌终端硬件	
                        主频	1.8GHz 存储：内存2G/内置存储8G
                        接口	后置DC12V*1，USB2.0*1，RJ45*1
                        操作系统	Android 5.1
                        可视面积	527、04 (H) x 296、46(V)mm
                        可视角度	178 度 (H) / 178 度 (V)
                        显示尺寸	≥ 23.8英寸
                        分辨率	1920*1080pixels
                        屏体级别	A 规屏/A 级屏/A+屏,A 级硬屏
                        屏体亮度	≥ 450 cd/㎡
                        显示模式	亮度可调
                        屏体对比度	≥1000:1
                        触摸方式	十点电容触摸
                        物理尺寸	约660mm(H) x 360mm(W) x 27、5mm(D)
                        音响	≥ 2*3W 8Ω
                        电压	DC 12V
                        整机功耗	＜60W
                        待机功率	≤0.5W
                        响应时间	≤20ms
                        使用时间	7×24H
                        结构特性	支持防盗；防雾、防水、防尘
                        整机尺寸	660*360*27.5mm，偏差±2mm；净重:7.5kg，偏差±0.5kg
                        支持竖挂和横挂	支持
                        功能特性	支持远程控制功能：包括远程开机、关机、重启、打铃，定时开机、关机、打铃。开机LOGO自定义
                        LED 背光源采用A 规液晶屏，屏幕表面采用4mm 厚度钢化玻璃，防划防撞；要求钢化玻璃可见光透射比不低于92%以上	符合
                        输出接口具备1 路DC IN，1 路网口，1 路USB 2.0，Wifi 网口控制，可远程开关机，定时开关机	符合
                        支持10级震动报警	支持
                        支持IP电话	支持
                </span>
                </p>
                </div>
                </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //纵向列表
            list:[]
        }
    },
    watch: {
        $route(){
            //路由参数改变页面重新渲染
            this.forListData()
        }
    },
    created(){
        this.forListData()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods: {
        //获取产品列表
        forListData(){
            this.list=[]
            let url=`${this.$store.state.ipfg}/goods/condition/${this.$route.query.id}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.list=resp.data.data
            })
        },
        //携带参数到产品详情页面
        // topro(index){
        //     // alert(index.id)
        //     this.$router.push({path:'/product_details',query:{'id':index.id}})
        //     let url=`${this.$store.state.ipfg}/goods/hot/${index.id}`
        //     console.log(url)
        //     Axios.get(url).then(resp=>{
        //         console.log(resp)
        //     })
        // }
        jup(){
            this.$router.push({path:'/electronicClassBrand'})
        }
    },
    store
}
</script>
<style scoped>

 .bg_top{
    height: 60px;
    /* background: url(../../../assets/topimg/topimg.png) no-repeat center center; */
    background-size:100%; 
}
.pru{
    font-size: 16px;
    color: #333333;
    line-height: 45px;
}
 /* 列表插图 */
 .lengthways{
    height: 170px;
    margin: 20px 0px;
    background: #FBFBFB;
    position: relative;
}
.lengthways>.img_size{
    display: inline-block;
    cursor: pointer;
}
 .lengthways>.img_size>img{
     border: 1px solid #CCCCCC;
     display: inline-block;
     width: 230px;
     height: 150px;
     margin: 9px;
 }
 .lengthways>.font{
     background: ccc;
     position: absolute;
     right: 0px;
     height: 130px;
     top: 0px;
     width: 660px;
     padding: 20px;
     overflow: hidden;
     /* border: 1px solid gold; */
 }
.lengthways>.font>p>.title{
    color: #4b8ada;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
    cursor: pointer;
}
.lengthways>.font>p>.particular{
    text-align: center;
    line-height: 20px;
    width: 80px;
    display: block;
    background: #4b8ada;
    height: 20px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}
.lengthways>.font>.introduction{
    font-size: 13px;
    line-height: 25px;
}
</style>



