<template>
    <div>
        <div>
            <h3 class="pru">产品列表</h3>
            <hr>
            <div   class="lengthways"><div   class="img_size">
                <img   src="./delete_img/xinxifabuzhuji.png" alt="" @click="jup">
                </div>
                 <div   class="font">
                     <p  >
                     <span   class="title" @click="jup">信息发布主机</span>
                      <span   class="particular" @click="jup">详细信息</span>
                      </p>
                       <p   class="introduction">
                           <span  >产品概述:</span> 
                           <span  >
                        编辑工作站	
                        处理器	Intel 第7代 酷睿i5 8400 3.1GHz
                        内存	32GB DDR4 2400
                        硬盘	希捷1T
                        显卡	集显
                        屏幕尺寸	24英寸 、宽屏16：9、1920×1080、IPS屏
                        通信接口	1000Mbps
                        操作系统	Windows10
                        Web服务器	
                        规格	2U机架式服务器
                        处理器	E5-2603V4 6核6线程 1.7主频
                        内存	64GB DDR4内存，至少配置20个DIMM插槽
                        硬盘	500G固态硬盘
                        网卡	1000M*2
                        电源	单电源
                        其他	24寸显示器，罗技键盘、鼠标
                        数据库服务器	
                        规格	2U机架式服务器
                        处理器	至强E5-2650V2CPU 8核心2.6主频
                        内存	16GB DDR4内存，至少配置20个DIMM插槽
                        硬盘	500G固态硬盘，2TB数据硬盘
                        网卡	1000M*2
                        电源	单电源
                        其他	24寸显示器，罗技键盘、鼠标
                </span>
                </p>
                </div>
                </div>
            
        </div>
    </div>
</template>
<script>
import Axios from 'axios';
import store from '../../vuex/store';
export default {
    data() {
        return {
            //纵向列表
            list:[]
        }
    },
    watch: {
        $route(){
            //路由参数改变页面重新渲染
            this.forListData()
        }
    },
    created(){
        this.forListData()
        window.scrollTo(0,0)//加载路由的时候返回网页顶部
    },
    methods: {
        //获取产品列表
        forListData(){
            this.list=[]
            let url=`${this.$store.state.ipfg}/goods/condition/${this.$route.query.id}`
            Axios.get(url).then(resp=>{
                console.log(resp)
                this.list=resp.data.data
            })
        },
        //携带参数到产品详情页面
        // topro(index){
        //     // alert(index.id)
        //     this.$router.push({path:'/product_details',query:{'id':index.id}})
        //     let url=`${this.$store.state.ipfg}/goods/hot/${index.id}`
        //     console.log(url)
        //     Axios.get(url).then(resp=>{
        //         console.log(resp)
        //     })
        // }
        jup(){
            this.$router.push({path:'/informationPublishingHost'})
        }
    },
    store
}
</script>
<style scoped>

 .bg_top{
    height: 60px;
    /* background: url(../../../assets/topimg/topimg.png) no-repeat center center; */
    background-size:100%; 
}
.pru{
    font-size: 16px;
    color: #333333;
    line-height: 45px;
}
 /* 列表插图 */
 .lengthways{
    height: 170px;
    margin: 20px 0px;
    background: #FBFBFB;
    position: relative;
}
.lengthways>.img_size{
    display: inline-block;
    cursor: pointer;
}
 .lengthways>.img_size>img{
     border: 1px solid #CCCCCC;
     display: inline-block;
     width: 230px;
     height: 150px;
     margin: 9px;
 }
 .lengthways>.font{
     background: ccc;
     position: absolute;
     right: 0px;
     height: 130px;
     top: 0px;
     width: 660px;
     padding: 20px;
     overflow: hidden;
     /* border: 1px solid gold; */
 }
.lengthways>.font>p>.title{
    color: #4b8ada;
    font-size: 14px;
    font-weight: normal;
    line-height: 30px;
    cursor: pointer;
}
.lengthways>.font>p>.particular{
    text-align: center;
    line-height: 20px;
    width: 80px;
    display: block;
    background: #4b8ada;
    height: 20px;
    color: #fff;
    font-size: 14px;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}
.lengthways>.font>.introduction{
    font-size: 13px;
    line-height: 25px;
}
</style>



